<template lang="html">
  <div class="" style="position:relative;">
    <div class="headers-wrapper" style="border-bottom: 1px solid #e6e9f2;">
      <headers></headers>
    </div>
    <div id="b-body">
    	<div class="one-title">
    		<div>
    			<h2>
	    			付款记录
	    		</h2>
	    		<span>
	    			/
	    		</span>
	    		<p>
	    			说明说明说明说明说明说明说明说明
	    		</p>
    		</div>
    		<div>
    			<router-link tag='el-button' class='btn-one' to='/errorlogging'>查看错误记录</router-link>
    			<el-button class='btn-two'>EXCEL导出</el-button>
    		</div>
    	</div>
    	<router-view></router-view>
    	<div class="s-body">
    		<div class="body-one">
    			<el-form label-width="102px">
					  <el-form-item label="运单号">
					    <el-input placeholder='请输入订单号'></el-input>
					  </el-form-item>
					  <el-form-item label="收款人">
					    <el-input placeholder='选择'></el-input>
					  </el-form-item>
					  <el-form-item label="本页显示笔数">
					    <el-select v-model="valueA">
						    <el-option
						      v-for="item in options"
						      :key="item.value"
						      :label="item.label"
						      :value="item.value">
						      <span style="float: left">{{ item.label }}</span>
						      <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
						    </el-option>
						  </el-select>
					  </el-form-item>
					</el-form>
	    		<el-form label-width="102px">
						  <el-form-item label="司机姓名">
