						  </el-form-item>
						  <el-form-item label="支付单号">
						    <el-input placeholder='请填写支付单号'></el-input>
						  </el-form-item>
						</el-form>
	    		<el-form label-width="102px">
						  <el-form-item label="司机手机号">
						    <el-input placeholder='请输入司机手机号'></el-input>
						  </el-form-item>
						  <el-form-item label="申请时间">
						  	<el-date-picker
						      v-model="valuea"
						      type="date"
						      placeholder="选择日期"
						      :picker-options="pickerOptionsa">
						    </el-date-picker>
						  </el-form-item>
						  <el-form-item label="未支付">
						    <el-select v-model="value">
							    <el-option
							      v-for="item in money"
							      :key="item.value"
							      :label="item.label"
							      :value="item.value">
							      <span style="float: left">{{ item.label }}</span>
							      <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
							    </el-option>
							  </el-select>
						  </el-form-item>
						</el-form>
	    		<el-form label-width="102px">
						  <el-form-item label="车牌号">
						    <el-input placeholder='请输入车牌号'></el-input>
						  </el-form-item>
						  <el-form-item label="支付时间">
						  	<el-date-picker
						      v-model="value1"
						      type="date"
						      placeholder="选择日期"
						      :picker-options="pickerOptions0">
						    </el-date-picker>
						  </el-form-item>
						  <el-form-item label="">
	    					<el-button type="primary" style='width: 175px;'>查询</el-button>

						  </el-form-item>
						</el-form>
	    	</div>
    		<div class="body-two">
    			<div>
    				<p>
	    				预申请：
		    			<span>
		    				0.0
		    			</span>元
	    			</p>
	    			<p>
	    				已申请：
		    			<span>
		    				0.0
		    			</span>元
	    			</p>
	    			<p>
	    				已支付：
		    			<span>
		    				0.0
		    			</span>元
	    			</p>
	    			<p>
	    				已到账：
		    			<span>
		    				3452.21
		    			</span>元
	    			</p>
    			</div>

    			<p class="p-right">
    				合计：
    				<span>
    					213123123
    				</span>元
    			</p>
    		</div>
    		<div class="body-three">
    			<el-table
				    ref="multipleTable"
				    :data="tableData3"
				    border
				    tooltip-effect="dark"
				    style="width: 100%"
				    @selection-change="handleSelectionChange">
				    <el-table-column
				      type="selection"
				      width="55">
				    </el-table-column>
				    <el-table-column
				      label="支付单号"
				      min-width="95">
				      <template scope="scope">{{ scope.row.date }}</template>
				    </el-table-column>
				    <el-table-column
				      prop="name"
				      label="运单号"
				      min-width="80">
				    </el-table-column>
				    <el-table-column
				      prop="name"
				      label="司机姓名"
				      min-width="95">
				    </el-table-column>
				    <el-table-column
				      prop="name"
				      label="司机手机号"
				      min-width="110">
				    </el-table-column>
				    <el-table-column
				      prop="name"
				      label="车牌号"
				      min-width="80">
				    </el-table-column>
				    <el-table-column
				      prop="name"
				      label="运费金额"
				      min-width="95">
				    </el-table-column>
				    <el-table-column
				      prop="name"
				      label="支付方式"
				      min-width="95">
				    </el-table-column>
				    <el-table-column
				      prop="name"
				      label="支付状态"
				      min-width="95">
				    </el-table-column>
				    <el-table-column
				      prop="name"
				      label="申请人"
				      min-width="80">
				    </el-table-column>
				    <el-table-column
				      prop="name"
				      label="付款人"
				      min-width="80">
				    </el-table-column>
				    <el-table-column
				      prop="name"
				      label="申请时间"
				      min-width="95">
				    </el-table-column>
				    <el-table-column
				      prop="name"
				      label="申请时间"
				      min-width="95">
				    </el-table-column>

				    <el-table-column
				      label="操作"
				      min-width="140">
				      <template scope="scope">
                <el-button type="text">轨迹</el-button>
                <router-link :to="{path:'/motorcade/mycar/tx'}">
                  <el-button type="text">短信</el-button>
                </router-link>
				        <el-button type="text">打印</el-button>
				      </template>

				    </el-table-column>
				  </el-table>
    		</div>
				<div class="body-four">
					<el-form :inline="true" :model="formInline" class="demo-form-inline">
					  <el-form-item label="操作功能：">
              <btnp></btnp>
					  </el-form-item>
					</el-form>
					<div class="block">
					  <el-pagination
					    layout="prev, pager, next"
					    :total="100">
					  </el-pagination>
					</div>
				</div>
    	</div>

    </div>
    <!--<router-view></router-view>-->
  </div>
</template>

<script>
import headers from "@/components/header"
import btnp from "@/page/LYL-btn-PaymentMethod"
export default {
  components:{
    headers,
    btnp
  },
	data(){
		return{
			formInline: {
          user: '',
          region: ''
       },
			 input: '',
			  options: [{
          value: '选项1',
          label: '30笔'
        }, {
          value: '选项2',
          label: '20笔'
        }, {
          value: '选项3',
          label: '40笔'
        }, {
          value: '选项4',
          label: '3110笔'
        }, {
          value: '选项5',
          label: '10086笔'
        }],
        money:[{
        	value:'选项1',
        	label:"已支付"
        },{
        	value:"选项2",
        	label:'未支付'
        }],
        value: '',
        valueA: '',
        pickerOptions0: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        pickerOptions1: {
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
        pickerOptionsa: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        pickerOptionsb: {
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        valuea: '',
        valueb: '',
        tableData3: [{
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-08',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-06',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-07',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }],
        multipleSelection: [],
		}
	},
  methods: {
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      }
    }
}
</script>

<style lang="less" scopt>
	.headers-wrapper{
		background: #FFFFFF;
	}
	body{
		background: #fafbff;
		font-family:"微软雅黑";
	}
	#b-body{
		width: 1200px;
		margin: 0 auto;
	}
	.one-title{
		padding: 28px 0;
		display: flex;
		justify-content: space-between;
		color: #bfc3d1;
		>div{
			display: flex;
			align-items: center;
		}
		h2{
			color: #0c2482;
		}
		span{
			margin: 0 8px;
		}
		.btn-one{
			color: #dbddea;
			border-color: #dbddea;
		}
		.btn-two{
			color: #4c974e;
			border-color: #4c974e;
		}
	}
	.s-body{
		font-size: 10px;
		border: 1px solid #ccc;
		box-sizing: border-box;
		width: 1200px;
		background: #FFFFFF;
		padding: 0 22px;
		.body-one{
			margin-top: 24px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom:1px solid #ccc;
			label{
				color: #6275b2;
			}
			.el-input__inner{
				background:#f5f6fb;
			}
		}
		.body-two{
			background: #fffbfd;
			border: 1px solid #ccc;
			padding: 6px;
			display: flex;
			justify-content: space-between;
			color: #999;
			margin: 10px 0;
			p{
				margin-right: 2em;
				float: left;
			}
			.p-right{
				margin: 0;
			}
			span{
				color: red;
			}
		}
		.body-three{
			font-size: 10px;
		}
		.body-four{
			margin-top: 20px;
			display: flex;
			justify-content: space-between;
			color: #CCCCCC;

		}
	}
</style>



