<template lang="html">
	<!--支付-->
	<div class="">
		<el-button type="primary" @click="xxx()">确认付款</el-button>
		<div id="bBox" v-show="bshow">
		  <div class="box">
		    <div class="s-box">
		    	<div class="s-header">
		    		<span>运费支付</span>
		    		<span class="s-delete" @click="goDie()">×</span>
		    	</div>
			    <hr />
			    <div class="s-kuang">
			    	<div class="s-ge-one">
			    		<div>
			    			支付方式
			    		</div>
			    		<div>
			    			<div>
			    				<input type="radio" name="" id="" value="" />
			    				保理支付
			    				<p>
			    					可用额度：<span>123123123.00元</span>
			    				</p>
			    			</div>
			    			<div>
			    				<input type="radio" name="" id="" value="" />
			    				保理支付
			    				<p>
			    					可用额度：<span>123123123.00元</span>
			    				</p>
			    			</div>
			    		</div>
			    	</div>
			    	<div class="s-ge-two">
			    		本次支付运费合计：<span>12132341元</span>
			    	</div>
			    	<div class="s-ge-three">
			    		<div>
			    			输入支付密码
			    		</div>
			    		<div>
			    			<input type="password" style="background: #eee;"/>
			    		</div>
			    	</div>

			    </div>
			    <el-button type="primary" class='btn-one' @click="goDie()">确认支付</el-button>

		    </div>
		  </div>
		</div>
	</div>

</template>

<script>

export default{
	data(){
		return{
			bshow: false
		}
	},
	methods:{
		goDie(){
			this.bshow = false;
		},
		xxx(){
			this.bshow = true;
		}
	}
}
</script>

<style lang="less" scopt>
body{
	font-family: "微软雅黑";
}
.s-btn{
	background: green;
	color: #fff;
}
*{
	box-sizing: border-box;
}
	.box{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 999;
		background: rgba(0,0,0,0.5);
		.s-box{
			width: 700px;
			background: #fff;
			margin: 30px auto 0 auto;
			border-radius:5px;
			margin: auto;
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			height: 350px;
			.s-header{
				padding: 16px;
				display: flex;
				justify-content: space-between;
				font-size: 16px;
				.s-delete{
					cursor: pointer;
				}
			}

			.s-kuang{
				padding: 16px;
				font-size: 14px;
				.s-ge-one{
					border: 1px solid #CCCCCC;
					display: flex;
					justify-content: space-between;
					>div:first-child{
						width: 25%;
						text-align: center;
						border-right: 1px solid #CCCCCC;
						font-size: 16px;
						line-height: 80px;
					}
					>div:last-child{
						width: 75%;
						input{
							margin: 0 10px 0 15px;
							width: 16px;
						}
						p{
							margin-left: 16px;
							span{
								color: red;
							}
						}
						div{
							display: flex;
							align-items: center;
							line-height: 40px;
						}
						>div:first-child{
							border-bottom: 1px solid #CCCCCC;
						}
					}
				}
				.s-ge-two{
					line-height: 40px;
					text-align: center;
					border: 1px solid #CCCCCC;
					border-top: none;
					span{
						color: red;
					}
				}
				.s-ge-three{
					display: flex;
					justify-content: space-between;
					border: 1px solid #CCCCCC;
					border-top: none;
					>div:first-child{
						width: 25%;
						text-align: center;
						border-right: 1px solid #CCCCCC;
						font-size: 16px;
						line-height: 40px;
					}
					>div:last-child{
						width: 75%;
						line-height: 40px;
						input{
							margin-left: 15px;
							border: 1px solid #E2F0E4;
						}
					}
				}
			}
		}
		.btn-one{
			background: green;
			color: #fff;
			margin:14px ;
			float: right;

		}
	}
	.s-box:after{
		content: '';
		display: block;
		clear: both;
	}
</style>



